.container-news {
  position: relative;
  width: 300px;
  margin: 0 auto;
  border: 1px solid #dddedf;
  border-radius: 4px;
  background-color: #ffffff;
  padding-bottom: 12px;
}
.container-news-header,
.container-news-options,
.container-news-report {
  width: 100%;
  margin: 0 auto;
}
.container-news-header-category {
  width: 100%;
  padding: 10px;
  justify-content: space-between;
  background-color: rgba(236, 236, 241, 0.5);
}
.container-news-header-category__text-container {
  width: 55%;
}
.container-news-header-category__text {
  font-size: 1.28rem;
  font-family: "Roboto Mono", monospace;
  font-weight: 700;
  line-height: 1.7rem;
  color: #304c70;
}
.container-news-header-category__text-hour {
  font-size: 1.28rem;
  font-family: "Roboto Mono", monospace;
  font-weight: 400;
  line-height: 1.5rem;
  color: #49494f;
  text-align: end;
}
.container-news-header-category__text span img,
.container-news-header-category__text-hour span img {
  width: 15px;
  vertical-align: middle;
  margin-right: 5px;
  color: #49494f;
}
.container-news-header-category__subtitle {
  font-size: 1.6rem;
  font-family: "Roboto Mono", monospace;
  font-weight: 700;
  line-height: 2rem;
  color: #7598c4;
}
hr {
  border: 1px solid #ececf1;
}
.container-news-header-author {
  padding: 10px;
}
.container-news-header-author-name {
  height: 21px;
  line-height: 1.7rem;
  font-size: 1.28rem;
  font-weight: 700;
  font-family: "Roboto Mono", monospace;
  color: #a3a3a8;
}
.container-news-header-author-title {
  line-height: 2.08rem;
  font-size: 1.76rem;
  font-weight: 700;
  font-family: "Roboto Mono", monospace;
  color: #49494f;
  gap: 10px;
  letter-spacing: -1px;
}
.container-news-header-author-title img {
  height: 35px;
  width: 35px;
  border-radius: 50%;
}
.container-news-report {
  padding: 10px;
}
.container-news-report div {
  margin-bottom: 15px;
  font-size: 1.47rem;
  font-family: "Roboto Mono", monospace;
  line-height: 2rem;
  color: #49494f;
}
.container-news-report img {
  width: 100%;
}
.container-news-options {
  margin-top: 10px;
  justify-content: space-between;
}
.container-news-option {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 30%;
  gap: 5px;
}
.container-news-option p {
  font-size: 1.6rem;
  font-weight: 400;
  font-family: "Roboto Mono", monospace;
  line-height: 2rem;
  color: #49494f;
}
.container-news-option img {
  width: 21px;
}
@media (min-width: 719px) {
  .container-news {
    width: 500px;
  }
}
@media (min-width: 1000px) {
  .container-news {
  }
}
